.card{
    width: 10rem;
    /* height: 13rem; */
    /* position: absolute; */
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    align-items: center;
    text-align: center;
    background: rgba(255, 255, 255, 0.26);
    border: 7px solid var(--pink);
    box-shadow: var(--boxShadow);
    border-radius: 20px;
    padding: 0px 20px 2rem 20px;    
}

.card>img{
    transform: scale(0.6);
    margin-bottom: -2rem;
}

.card span:nth-of-type(2){
    color: var(--gray);
    font-size: 16px;
}

.c-button{
    background: #fff;
    box-shadow: 0px 19px 60px rgba(0, 0, 0, 0.08);
    border-radius: 7px;
    border: none;
    padding: 10px;
    font-size: 16px;
    color: #5290fd;

}
